import { DateTime } from "luxon";

let BASE_URL = "https://api.openweathermap.org/data/2.5";
let API_KEY = "9b09ee71480be160d500b9de5d20a0cb";

const getWeatherData = (infoType, searchParams) => {
  const url = new URL(process.env.REACT_APP_BASEURL + "/" + infoType);

  url.search = new URLSearchParams({ ...searchParams, appid: API_KEY });

  console.log(url);

  return fetch(url).then((res) => res.json());
};

const formatCurrentData = (data) => {
  const {
    coord: { lat, lon },
    main: { temp, feels_like, temp_min, temp_max, humidity },
    name,
    dt,
    sys: { country, sunrise, sunset },
    weather,
    wind: { speed },
  } = data;

  const { main: description, icon } = weather[0];

  return {
    lat,
    lon,
    temp,
    feels_like,
    temp_min,
    temp_max,
    icon,
    description,
    humidity,
    name,
    dt,
    country,
    sunrise,
    sunset,
    weather,
    speed,
  };
};

const formatForecastWeather = (data) => {
  let { timezone, daily, hourly } = data;
  daily = daily.slice(1, 6).map((d) => {
    return {
      title: formatToLocalTime(d.dt, timezone, "ccc"),
      temp: d.temp.day,
      icon: d.weather[0].icon,
    };
  });

  hourly = hourly.slice(1, 15).map((d) => {
    return {
      title: formatToLocalTime(d.dt, timezone, "hh:mm a"),
      temp: d.temp,
      icon: d.weather[0].icon,
    };
  });

  return { timezone, daily, hourly };
};

const getFormattedWeatherData = async (searchParams) => {
  const currentWeatherFormatted = await getWeatherData(
    "weather",
    searchParams,
  ).then(formatCurrentData);

  const { lat, lon } = currentWeatherFormatted;

  const formattedWeatherForecast = await getWeatherData("onecall", {
    lat,
    lon,
    exclude: "current, minutely,alerts",
    units: searchParams.units,
  }).then(formatForecastWeather);

  return { ...currentWeatherFormatted, ...formattedWeatherForecast };
};

const formatToLocalTime = (
  secs,
  zone,
  format = "cccc, dd LLL yyyy' | Local time: 'hh:mm a",
) =>
  DateTime.fromSeconds(secs)
    .setZone(zone)
    .toFormat(format);

const iconUrl = (code) => `http://openweathermap.org/img/wn/${code}@2x.png`;

export default getFormattedWeatherData;

export { formatToLocalTime, iconUrl };
